import React from "react";
import { Link } from "react-router-dom";

export default function HomePage() {
  return (
    <main className="home">
      <HeroSection />
      <AboutSection />
      <TreatmentsSection />
      <ContactSection />
    </main>
  );
}

const HeroSection = () => (
  <section className="hero-section">
    <div className="hero-content">
      <h1>    </h1>
      <p>   </p>
      <br />
      <button>
        <Link to="/contact"> </Link>
      </button>
    </div>
  </section>
);
const AboutSection = () => (
  <section className="about-section">
    <div className="about-container">
      <div className="about-content">
        <div className="about-image-container">
          <img src="orna.webp" alt=" " loading="lazy" width="400px" height="500px" />
        </div>
        <div className="about-text-container">
          <h2> </h2>
          <p>
            -     ,   .

<br/>           .     ,        .
          </p>
          <Link className="contact-link" to="/contact">   </Link>
        </div>
      </div>
    </div>
  </section>
);

export const TreatmentsSection = ({
  additionalTreatments,
  showLink = true,
  showTitle = true,
}) => {
  let treatments = [
    {
      title: " ",
      image: "baby-massage.webp",
      url: "/babies",
    },
    {
      title: "Sound Healing",
      image: "soundHealing.webp",
      url: "/sound",
    },
    {
      title: "  ",
      image: "pregnancy-massage.webp",
      url: "/pregnant",
    },
    {
      title: " ",
      image: "cups.webp",
      url: "/cups",
    },
    {
      title: " ",
      image: "crystals-massage.webp",
      url: "/crystals",
    },
  ];

  if (Array.isArray(additionalTreatments)) {
    treatments = [...treatments, ...additionalTreatments];
  }

  return (
    <section className="treatments-section">
      {showTitle && <h2></h2>}
      <div className="treatment-cards">
        {treatments.map((treatment, index) => (
          <button className="treatment-card" key={index}>
             <Link to={treatment.url}>
            <img
              src={treatment.image}
              alt={treatment.title}
              loading="lazy"
              className="treatment-image"
            />
            <p className="treatment-title">
            </p>
            {treatment.title}</Link>
          </button>
        ))}
      </div>
      <br />
      <br />
      <br />
      {showLink && (
        <Link className="more" to="/treatments">
           
        </Link>
      )}
      <br />
      <br />
    </section>
  );
};

const ContactSection = () => (
  <section className="contact-section">
    <h2> </h2>
    <p>:  ,  9</p>
    <p>: 054-9794-777</p>
    <p>: orenkl162@gmail.com</p>
    <p> : '-' 9:00-17:00, '  </p>
  </section>
);
